export const configureRequest =
{
	baseUrl: process.env.VUE_APP_BASE_URL,
	getDefaultOptions(additionalHeaders = {})
	{
		const headers = {};
		Object.entries({
			'Content-Type': 'application/json',
			...additionalHeaders,
		}).map(([header, value]) =>
		{
			if (typeof value !== 'undefined')
			{
				headers[header] = value;
			};
			return headers;
		});
		return {
			headers,
		};
	},

};
