	getPlayerOfLigue(data)
	{
		return fetch(`${configureRequest.baseUrl}v2/players/${data.leagueId}?club=${data.club}&pos=${data.pos}`, {
			...configureRequest.getDefaultOptions(),
			method: 'GET',
		});
	},
	getPlayersOfLigue(leagueId)
	{
		return fetch(`${configureRequest.baseUrl}v2/players/${leagueId}?nft=only`, {
			...configureRequest.getDefaultOptions(),
			method: 'GET',
		});
	},
