import Vue from 'vue';
import VueRouter from 'vue-router';
import MainView from '@/views/MainView/MainView.vue';
import InfoPage from '@/views/InfoPage/InfoPage.vue';
import TransferPage from '@/views/TransferPage/TransferPage.vue';
import TransferPageWithCards from '@/views/TransferPageWithCards/TransferPageWithCards.vue';
import UserCards from '@/views/UserCards/UserCards.vue';
import TournamentPage from '@/views/Tournament/TournamentPage.vue';
import TournamentDetails from '@/views/TournamentDetails/TournamentDetails.vue';

Vue.use(VueRouter);

const routes = [
	{
		path: '/main',
		name: 'main',
		component: MainView
	},
	{
		path: '/info-page',
		name: 'InfoPage',
		component: InfoPage
	},
	{
		path: '/transfer-page',
		name: 'TransferPage',
		component: TransferPage
	},
	{
		path: '/transfer-page-cards',
		name: 'TransferPageWithCards',
		component: TransferPageWithCards
	},
	{
		path: '/',
		name: 'UserCards',
		component: UserCards
	},
	{
		path: '/tournament/:id',
		name: 'TournamentPage',
		component: TournamentPage
	},
	{
		path: '/tournament-details/:id',
		name: 'TournamentDetails',
		component: TournamentDetails
	},
];

const router = new VueRouter({
	mode: 'history',
	base: process.env.BASE_URL,
	routes
});

export default router;
