import { baseApi } from '@/api/BaseApi';
export default {
	state: () => ({
		teams: null,
		allTeam: null,
	}),
	mutations: {
		createTeam(state, teams)
		{
			state.teams = teams;
		},
		getTeam(state, teams)
		{
			state.allTeam = teams;
		}
	},
	actions: {
		async CREATE_TEAM({ commit }, data)
		{
			try
			{
				const response = await baseApi.createTeam(data);
				const json = await response.json();
				commit('createTeam', json.response);
			}
			catch (e)
			{
				console.log('err: ', e);
			};
		},
		async GET_TEAM({ commit })
		{
			try
			{
				const response = await baseApi.getAllTeamsOfUser();
				const json = await response.json();
				commit('getTeam', json.response);
			}
			catch (e)
			{
				console.log('err: ', e);
			};
		}
	},
};
