<template>
	<div class="mb-4 bg-theme">
		<div class="border-xxs">
			<button v-if="mainPage" @click = "accordionOpen" class="btn accordion-btn no-effect collapsed  border-bottom-none font-18">
				<i class="fa fa-trophy color-yellow-dark me-2"></i>
				{{name}}
				<i class="fa fa-chevron-down font-10 accordion-icon"></i>
			</button>
			<button v-else @click = "accordionOpen" class="btn accordion-btn no-effect border-none font-18 mx-1  ms-0 collapsed">
				<div class="d-flex justify-between ">
					<div class="d-flex">
						<img src="" alt="" class="img-accord preload-img entered loaded">
						<div class="font-14 ms-3 mt-3">
							<p class="font-14 color-theme mb-1">{{name}}</p>
							<p class="font-10 mb-1">{{position}}({{club}})</p>
						</div>
					</div>
					<div class="align-self-center">
						<p class="mb-0 color-theme font-12 ">
							{{points}} pts
							<i class="fa fa-chevron-down font-10 accordion-icon ms-3 fa-rotate-180"></i>
						</p>
					</div>
				</div>
			</button>
			<div :class='{active: isOpen}' class="accordion-content" :style="{height: test + 'px'}">
				<div class="mb-3 me-3" ref="content">
					<div class="tournamet" v-for="tournament of tournaments" :key="tournament.ID">
						<div v-if="mainPage" style="height: 170px;" class="card ms-3 shadow-l mb-0 border-xxs">
							<div class="card-top mx-3 my-3">
								<p class="color-theme">
									<i class="fa fa-trophy me-1">
										{{slug}}
									</i>
								</p>
							</div>
							<div class="card-center mx-3">
								<a href="#" class="font-16 color-theme">{{tournament.Name}}</a>
							</div>
							<div class="card-bottom">
								<div class="d-flex mx-3 my-3 align-items-baseline justify-between">
									<div class="">
										<a @click = '$emit("showPopup", {tournament, slug})' class="btn btn-s bg-blue-dark rounded-sm font-700 tex.t-uppercase">join</a>
									</div>
									<div class="ms-3">
										<span v-if = "price" class="d-block opacity-70 font-11 mt-n2 color-theme">
											{{price}} ton
										</span>
									</div>
								</div>
							</div>
							<div class="card-overlay bg-theme"></div>
						</div>
					</div>
					<div class="ps-0 pe-3 content">
						<div class="d-flex align-items-baseline justify-between"
							v-for="(detail, index) of pointsDetails"
							:key="index"
						>
							<div class="">{{detail.stat}}</div>
							<div class="ms-3">{{detail.pts}} pts</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
export default {
	name: 'GlobalAccordion',
	props:
	{
		name:
		{
			type: String,
			default: ''
		},
		position:
		{
			type: String,
			default: ''
		},
		club:
		{
			type: String,
			default: ''
		},
		slug:
		{
			type: String,
			default: ''
		},
		type:
		{
			type: String,
			default: ''
		},
		price:
		{
			type: Number,
			default: null
		},
		points:
		{
			type: Number,
			default: null
		},
		mainPage: Boolean,
		tournaments: Array,
		pointsDetails: Array
	},
	data()
	{
		return {
			isOpen: false,
			test: 0,
		};
	},
	methods:
	{
		accordionOpen()
		{
			this.isOpen = !this.isOpen;
			if (this.isOpen)
				this.test = this.getHeight + 20;
			else
				this.test = 0;
		},
	},
	computed:
	{
		getHeight()
		{
			return this.$refs.content.clientHeight;
		}
	}
};
</script>
