import { baseApi } from '@/api/BaseApi';
export default {
	state: () => ({
		tournament: null,
		standings: null,
	}),
	mutations: {
		getTournament(state, tournament)
		{
			state.tournament = tournament;
		},
		getTeamStandings(state, standings)
		{
			state.standings = standings;
		}
	},
	actions: {
		async GET_TOURNAMENT({ commit }, id)
		{
			try
			{
				const response = await baseApi.getTourmentInfo(id);
				const json = await response.json();
				commit('getTournament', json.response);
			}
			catch (e)
			{
				console.log('err: ', e);
			};
		},
		async GET_TEAM_STANDINGS({ commit }, id)
		{
			try
			{
				const response = await baseApi.getTeamStandings(id);
				const json = await response.json();
				console.log('jsonsss: ', json);
				commit('getTeamStandings', json.response);
			}
			catch (e)
			{
				console.log('err: ', e);
			};
		},
	}
};
