import { baseApi } from '@/api/BaseApi';
export default {
	state: () => ({
		players: null,
		player: null,
	}),
	mutations: {
		getPlayers(state, players)
		{
			state.players = players;
		},
		getPlayer(state, players)
		{
			console.log('players: ', players);
			state.players = players;
		}
	},
	actions: {
		async GET_PLAYERS({ commit })
		{
			try
			{
				const response = await baseApi.getPlayersOfLigue(39);
				const json = await response.json();
				commit('getPlayers', json.response);
			}
			catch (e)
			{
				console.log('err: ', e);
			};
		},
		//     
		async GET_PLAYER({ commit }, data)
		{
			try
			{
				const response = await baseApi.getPlayerOfLigue(data);
				const json = await response.json();
				commit('getPlayer', json.response);
			}
			catch (e)
			{
				console.log('err: ', e);
			};
		}
	}
};
