import { baseApi } from '@/api/BaseApi';
export default {
	state: () => ({
		nft: null,
		clubs: null,
		standings: null,
	}),
	mutations: {
		getNft(state, nft)
		{
			console.log('nft: ', nft);
			state.nft = nft;
		},
		getClubs(state, clubs)
		{
			console.log('clubs: ', clubs);
			state.clubs = clubs;
		},
		getStandings(state, standings)
		{
			console.log('standings: ', standings);
			state.standings = standings;
		},
	},
	actions: {
		async GET_NFT({ commit }, data)
		{
			try
			{
				const response = await baseApi.getNftList(data);
				const json = await response.json();
				commit('getNft', json.response);
			}
			catch (e)
			{
				console.log('err: ', e);
			};
		},
		async GET_CLUB({ commit }, id)
		{
			try
			{
				const response = await baseApi.getClubList(id);
				const json = await response.json();
				commit('getClubs', json.response);
			}
			catch (e)
			{
				console.log('err: ', e);
			};
		},
		async GET_STANDINGS({ commit })
		{
			try
			{
				const response = await baseApi.getStandings();
				const json = await response.json();
				commit('getStandings', json.response);
			}
			catch (e)
			{
				// console.log('err: ', e);
			};
		},
	},
	getters: {
		nft (state)
		{
			return state.nft;
		},
		clubs (state)
		{
			return state.clubs;
		},
		standings (state)
		{
			return state.standings;
		},
	}
};
