import { baseApi } from '@/api/BaseApi';
export default {
	state: () => ({
		standings: null,
	}),
	mutations:
	{
		getStandings(state, standings)
		{
			state.standings = standings;
		}
	},
	actions:
	{
		async GET_STANDINGST({ commit }, id)
		{
			try
			{
				const response = await baseApi.getTourmentStandings(id);
				const json = await response.json();
				commit('getStandings', json.response);
			}
			catch (e)
			{
				console.log('err: ', e);
			};
		}
	}
};
