			<h1 class="page-title">Your NFT</h1>

			<b-form-select
				v-model="selectedClub"
				:options="clubs"
				value-field="ID"
				text-field="name"
				class="custom-selector club-selector"
				@change="getClubs()"
			>
				<template #first>
					<b-form-select-option :value="0">All clubs</b-form-select-option>
				</template>
			</b-form-select>

			<div class="pos-filter">
				<div class="btn btn-default" :class="[selectedPos == 'G' ? 'active' : '']"  @click="filterByPos('G')">GK</div>
				<div class="btn btn-default" :class="[selectedPos == 'D' ? 'active' : '']"  @click="filterByPos('D')">DEF</div>
				<div class="btn btn-default" :class="[selectedPos == 'M' ? 'active' : '']" @click="filterByPos('M')">MID</div>
				<div class="btn btn-default" :class="[selectedPos == 'F' ? 'active' : '']"  @click="filterByPos('F')">FW</div>
			</div>

			<div class="row">
				<div class="col-6 pb-4" v-for="item in nft" :key="item.player_id">
					<div class="nft-card">
						<div class="nft-card-content">
							<img :src="item.image" class="preload-img img-fluid entered loaded nft-card-image" alt="">
						</div>
						<div class="nft-card-price">
							<span class="ton-logo"></span>
							599
						</div>
					</div>
				</div>
			</div>

