		</div>
	</div>
</template>
<script>
export default {
	name: 'UserCards',
	data()
	{
		return {
			selectedClub: 0,
			selectedPos: ''
		};
	},
	mounted()
	{
		this.getClubs();
		// this.$store.dispatch('GET_NFT', { club: 42, leagued: 39, pos: 'M' });
		this.$store.dispatch('GET_CLUB', 39);
		this.$store.dispatch('GET_STANDINGS');
		// console.log(this.$store.state.players.player_list);
		// this.nft = this.$store.state.nft;
		// this.standings = this.$store.state.standings;
	},
	methods:
	{
		filterByPos(p)
		{
			this.selectedPos = p;
			this.getClubs();
		},
		getClubs()
		{
			this.$store.dispatch('GET_NFT', { club: this.selectedClub, leagued: 0, pos: this.selectedPos });
		}
	},
	computed: {
		nft ()
		{
			return this.$store.getters.nft;
		},
		clubs ()
		{
			return this.$store.getters.clubs;
		}
	},
};
</script>

