<template>
	<div class="create-team">
		<div class="create-team__title mt-n1">
			<h2>Add team</h2>
			<a href="#" class="close-menu" @click="$emit('close')"><i class="fa fa-times"></i></a>
		</div>
		<div class="content mb-0 mt-2">
			<div class="divider mb-3"></div>
			<p class="mb-3">
				Please enter the name of the team to participate in the tournament
			</p>
			<form action="input-style no-borders no-icon validate-field" @submit.prevent = "createTeam">
				<input v-model = "teamName" type="" class="form-control validate-text" placeholder="Team name">
				<button class="btn btn-l mt-4 rounded-sm btn-full bg-blue-dark text-uppercase font-800">add</button>
			</form>
		</div>
	</div>
</template>
<script>
export default {
	name: 'CreateTeam',
	props:
	{
		dataObj: Object,
	},
	data()
	{
		return {
			teamName: '',
			newObject: {},
		};
	},
	methods:
	{
		createTeam()
		{
			this.newObject = this.dataObj;
			this.newObject.Name = this.teamName;
			// console.log(this.newObject);
			this.$store.dispatch('CREATE_TEAM', this.newObject);
		}
	},
};
</script>
