import { baseApi } from '@/api/BaseApi';
export default {
	state: () => ({
		tonLink: '',
		wallet: null,
		showSombols: '',
	}),
	mutations: {
		getLink(state, tonLink)
		{
			state.tonLink = tonLink;
		},
		changeWallet(state, wallet)
		{
			state.wallet = wallet;
			let step = 0;
			for (let i = (wallet.length - 1); i >= 0; i--)
			{
				step++;
				if (step === 6)
					break;
				state.showSombols += wallet[i];
			};
			// console.log('ssss: ', state.showSombols);
		}
	},
	actions: {
		async GET_TON_LINK({ commit })
		{
			try
			{
				const response = await baseApi.getLink();
				const json = await response.json();
				commit('getLink', json.response);
			}
			catch (e)
			{
				// console.log('err: ', e);
			};
		},
		async CHANGE_WALLET({ commit })
		{
			try
			{
				const response = await baseApi.changeWallet();
				const json = await response.json();
				// console.log('store-wallets: ', json.response.wallet);
				commit('changeWallet', json.response.wallet);
			}
			catch (e)
			{
				// console.log('err: ', e);
			};
		},
		async SEND_CONNECT_INFO({ commit }, data)
		{
			try
			{
				// console.log('data: ', JSON.stringify(data));
				const response = await baseApi.tonConnectSdk(data);
				const json = await response.json();
				console.log('json: ', json);
				// commit('getLink', json.response);
			}
			catch (e)
			{
				// console.log('errTEst: ', e);
			};
		}
	},
	getters: {
		getTonLink(state)
		{
			return state.tonLink;
		}
	}
};
