<template>
	<div class="overlay" @click="clickOutside" ref = "popupOverlay">
		<slot></slot>
	</div>
</template>
<style lang="scss">
	.overlay
	{
		position: fixed;
		width: 100%;
		height: 100%;
		left: 0;
		top: 0;
		background: rgba(0, 0, 0, 0.35);
		visibility: hidden;
		opacity: 0;
		transition: .3s;
		z-index: 100;
		&.active
		{
			visibility: visible;
			opacity: 1;
			transition: .3s;
		}
	}
</style>
<script>
export default {
	methods:
	{
		clickOutside(e)
		{
			if (e.target === this.$refs.popupOverlay)
			{
				this.$emit('close');
			}
		}
	}
};
</script>
