import { baseApi } from '@/api/BaseApi';
export default {
	state: () => ({
		ligues: [],
	}),
	mutations: {
		getLigues(state, ligues)
		{
			state.ligues = ligues;
		}
	},
	actions: {
		async GET_LIGUES({ commit })
		{
			try
			{
				const response = await baseApi.getLigues();
				const json = await response.json();
				await baseApi.getToken();
				commit('getLigues', json.response);
			}
			catch (e)
			{
				console.log('err: ', e);
			};
		}
	},
	getters: {
		ligues(state)
		{
			return state.ligues;
		}
	}
};
